.container {
    display: flex;
    justify-content: left;
}

.loader {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 3  00;
}

.movie {
    display: grid;
    grid-template-columns: 400px 2fr 1fr;
    grid-template-rows: 100px 500px;
    grid-template-areas: 
    "header header header"
    "first second third"
    ;
    gap: 10px;
}

.header {
    grid-area: header;
    display: flex;
    justify-content: center;
}

.first {
    grid-area: first;
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.second {
    grid-area: second;
}

.third {
    grid-area: third;
}

.movie__img {
    background-color: white;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
        0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie__title {
    text-decoration: none;
    font-weight: 400;
}

.movie__year {
    text-decoration: none;
    font-weight: 400;
}

.movie__description {
    text-decoration: none;
    font-weight: 350;
}